.chronic-alcoholism,
.alcoholism-causes,
.alcoholism-factors {
    margin-bottom: 3rem;

    @media screen and (min-width: 768px) {
        margin-bottom: 4.4rem;
    }

    @media screen and (min-width: 1320px) {
        margin-bottom: 8.8rem;
    }
}

.chronic-alcoholism {
    
    @media screen and (min-width: 1320px) {
        padding-top: 2.5rem;
    }
}

.section__title--alcoholism-treatment {
    margin-bottom: 1.2rem;    
}

.section__description--alcoholism-treatment {
    margin-bottom: 2.1rem;

    @media screen and (min-width: 1320px) {
        max-width: 61rem;
        margin-bottom: 4.2rem;
    }
}

.check__list--chronic-alcoholism {
    display: flex;
    flex-wrap: wrap;

    @media screen and (min-width: 1320px) {
        margin: 0 -1.5rem;
    }

    .check__item {

        @media screen and (min-width: 1320px) {
            max-width: 59.5rem;
            margin: 1rem 1.25rem !important;
        }
    }
}