// Основной заголовок страницы H1
.main__title {
    @include OpenSans-Bold();
    font-size: 2rem;
    line-height: 3rem;
    color: $gray2;

    @media screen and (min-width: 414px) {
        font-size: 2.4rem;
        line-height: 3.3rem;
    }

    @media screen and (min-width: 768px) {
        font-size: 3.6rem;
        line-height: 4.9rem;
    }
}

// Заголовок секции H2
.section__title {
    text-align: center;
    @include OpenSans-Bold();
    font-size: 2rem;
    line-height: 3.3rem;
    color: $gray2;

    @media screen and (min-width: 414px) {
        font-size: 2.4rem;
        line-height: 3.7rem;
    }

    @media screen and (min-width: 1320px) {
        font-size: 3.6rem;
        line-height: 5.6rem;
    }
}

.section__title--light {
    @include OpenSans-Regular();
}

// Подзаголовок секции (блока)
.section__subtitle {
    @include OpenSans-Bold();
    font-size: 1.6rem;
    line-height: 2.8rem;
    color: $gray2;

    @media screen and (min-width: 768px) {
        font-size: 1.8rem;
    }

    @media screen and (min-width: 768px) {
        font-size: 2.4rem;
        line-height: 3.7rem;
    }
}

// Описание секции (блока)
// Список
.section__description,
.list {
    @include Raleway-Regular();
    font-size: 1.4rem;
    line-height: 2.3rem;
    color: $gray2;

    @media screen and (min-width: 768px) {
        font-size: 1.8rem;
        line-height: 2.8rem;
    }
}

.list-style li {
    @include OpenSans-Regular();
    font-size: 1.4rem;
    line-height: 2.5rem;
    color: $gray2;
}

.section__description {
    text-align: center;
}

// Обычный текст
.text {
    @include OpenSans-Regular();
    font-size: 1.4rem;
    line-height: 2.2rem;
    color: $gray2;
}

.text-italic {
    @include Raleway-Italic();
    font-size: 1.4rem;
    line-height: 2.2rem;
    font-feature-settings: "pnum" on, "lnum" on;
    color: $gray3;
}

// Заголовок карточки
.card__title {
    @include OpenSans-Bold();
    font-size: 2.4rem;
    line-height: 3.4rem;
    color: $gray2;
    font-feature-settings: "pnum" on, "lnum" on;
}

// Заголовок карточки (маленький)
.card__title--small {
    @include OpenSans-Bold();
    font-size: 1.8rem;
    line-height: 2.5rem;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: $gray2;
}

// Текст рядом с зелеными (градиентными) квадратными блоками
.counts__text {
    @include Raleway-Bold();
    font-size: 1.4rem;
    line-height: 2.3rem;
    color: $gray2;

    @media screen and (min-width: 414px) {
        font-size: 1.8rem;
    }
}

// Заголовок блока аккордиона
.accordion__title {
    @include Raleway-Bold();
    font-size: 1.4rem;
    line-height: 2rem;
    font-feature-settings: "pnum" on, "lnum" on;
    color: $white;

    @media screen and (min-width: 1320px) {
        font-size: 1.8rem;
        line-height: 2.5rem;
    }
}

// Текст ссылки наполитику конфиденциальности
.policy {
    @include OpenSans-Light();
    font-size: 1.2rem;
    line-height: 1.9rem;
    color: $gray4;

    @media screen and (min-width: 1320px) {
        line-height: 2.2rem;
    }
}

// Плейсхолдеры в инпутах
.placeholder {
    @include OpenSans-Regular();
    font-size: 1.4rem;
    line-height: 2.3rem;
    color: $gray5;

    @media screen and (min-width: 1320px) {
        font-size: 2.2rem;
        line-height: 1.4rem;
    }
}

.label {
    @include OpenSans-Bold();
    font-size: 1.8rem;
    line-height: 2.5rem;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: $gray6;
}

i {
    @include OpenSans-Bold();
    color: $green;
    font-style: normal;
}

strong {
    @include OpenSans-Bold();
    font-size: 1.8rem;
    line-height: 2.4rem;
    color: $gray2;
}

.check__item--warning {
    strong {
        color: $gray6;
    }
}

.method-block {
    strong {
        @include Raleway-Bold();
        font-size: 1.4rem;
        line-height: 2.2rem;
    }
}

.guarantees {
    strong {
        @include OpenSans-Bold();
        font-size: 1.6rem;
        line-height: 2.8rem;

        @media screen and (min-width: 1320px) {
            font-size: 2.4rem;
            line-height: 3.7rem;
        }
    }
}

.gray-block--guarantees {

    span {
        @include OpenSans-Regular();
    }
}

.breadcrumbs {
    @include OpenSans-Regular();
    font-size: 1.2rem;
    line-height: 1.7rem;
    color: $gray5;

    @media screen and (min-width: 1320px) {
        font-size: 1.4rem;
        line-height: 1.9rem;
    }

    a {
        color: $gray5; 
    }
}

.post {

    &__text {
        h2 {
            @include OpenSans-Bold();
            font-size: 2rem;
            line-height: 3.3rem;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: $gray2;

            @media screen and (min-width: 1320px) {
                font-size: 3.6rem;
                line-height: 5rem;
            }
        }
    
        p {
            @include OpenSans-Regular();
            font-size: 1.4rem;
            line-height: 2.2rem;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: $gray3;
        }
    }

    &__author {
        @include OpenSans-Bold();
        font-size: 1.8rem;
        line-height: 2.5rem;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: $gray2;
    }

    &__position {
        @include OpenSans-Regular();
        font-size: 1.4rem;
        line-height: 2rem;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: $gray5;
    }
}

.form__title {
    @include OpenSans-Bold();
    font-size: 2rem;
    line-height: 2.5rem;
    color: $gray2;

    @media (min-width: 768px) {
        font-size: 3.5rem;
        line-height: 4rem;
    }

    @media (min-width: 1320px) {
        font-size: 4.8rem;
        line-height: 5.8rem;
    }
}

.form__subtitle {
    @include OpenSans-Regular();
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: $gray2;

    @media (min-width: 1320px) {
        font-size: 1.8rem;
        line-height: 2.9rem;
    }
}

.form__policy-text,
.form__policy-link {
    @include OpenSans-Light();
    font-size: 1.4rem;
    line-height: 2.2rem;
    color: $gray4;

    @media (min-width: 1320px) {
        font-size: 1.4rem;
        line-height: 2.2rem;
    }
}