.our-specialists {

    @media screen and (min-width: 1320px) {
        margin-bottom: 10.3rem;
    }

    .specialists__content {
        width: 100%;
        min-height: 44.8rem;
        margin-bottom: 3rem;

        @media screen and (min-width: 1320px) {
            width: 50%;
            margin-bottom: 0;
        }

        &:first-of-type {

            @media screen and (min-width: 1320px) {
                margin-right: 2.2rem;
            }
        }
    }

    .content-block {

        @media screen and (min-width: 1320px) {
            flex-wrap: nowrap;
            align-items: flex-start;
        }
    }

    .specialists__photo {
        width: 25rem;
        height: 37rem;
        margin-bottom: 9rem;
        
        @media screen and (min-width: 1320px) {
            margin: 0 1.6rem 0 0;
        }
    }

    .specialists__experience {
        bottom: -7.7rem;
    }

    .specialists__name,
    .specialists__position {
        margin-bottom: .6rem;
    }

    .specialists__name {
        max-width: 24.9rem;
    }
    
    .specialists__info {
        margin: 0;
        display: flex;
        flex-direction: column;

        @media screen and (min-width: 1320px) {
            max-width: 32.9rem;
        }
    }

    .text {
        margin-bottom: 2.2rem;

        @media screen and (min-width: 1320px) {
            margin-bottom: 4.4rem;
        }
    }

    .btn--white {
        min-width: 18.6rem;
        padding: 1.6rem 1.1rem;
        margin-top: auto;
        align-self: flex-start;
    }
}