.services {
    padding: 2rem 0 4.1rem 0;

    @media screen and (min-width: 768px) {
        padding: 4.5rem 0;
    }

    @media screen and (min-width: 1320px) {
        padding: 4.8rem 0 6.5rem 0;
    }

    .btn--green {
        min-width: 11.5rem;
    }
}

.section__title--services {
    max-width: 45.5rem;
    margin: 1.1rem 0;
    text-align: center;

    @media screen and (min-width: 768px) {
        text-align: center;
    }

    @media screen and (min-width: 1320px) {
        max-width: initial;
        margin: 2.4rem 0 2.3rem 0;
        text-align: initial;
    }
}

.section__description--services {
    max-width: 63.7rem;
    margin-bottom: 3rem;
    text-align: center;

    @media screen and (min-width: 768px) {
        margin-bottom: 3.2rem;
    }

    @media screen and (min-width: 1320px) {
        margin-bottom: 2.6rem;
    }
}

.services-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    // margin: 0 -1.5rem;

    @media screen and (min-width: 1320px) {
        justify-content: flex-start;
        margin: 0 -1.5rem;
    }
}

.service__card {
    position: relative;
    width: 100%;
    max-width: 39.1rem;
    display: flex;
    flex-direction: column;
    margin: 1rem 0;

    @media screen and (min-width: 1320px) {
        width: calc(100% / 12 * 4);
        margin: 1.5rem 1.11rem;
    }

    .card__img {
        height: 18.2rem;
        margin-bottom: 1.7rem;
        border-radius: 1rem;

        img {
            height: 100%;
            border-radius: 1rem;
        }
    }

    .card__title {
        max-width: 30.8rem;
        margin-bottom: 1rem;
    }

    .card__text {
        max-width: 37rem;
        margin-bottom: 2.4rem;
    }

    .card__price {
        position: absolute;
        z-index: 6;
        top: 2.1rem;
        left: -0.5rem;
        min-width: 13rem;
        padding: 1rem;
        border-radius: 0 0.5rem 0.5rem 0;
        background: $green;
    }

    .btn--blue-service-card {
        min-width: 21.1rem;
        align-self: flex-start;
        margin-top: auto;
    }
}
