.check__item {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 1.5rem 2.3rem 1.5rem 4.35rem;
    @include OpenSans-Regular();
    font-size: 1.6rem;
    line-height: 2.3rem;
    color: $gray2;
    border-radius: .5rem;
    background: $gray6;

    @media screen and (min-width: 1320px) {
        padding: 2.25rem 3.3rem 2.25rem 8.7rem;
        font-size: 1.8rem;
        line-height: 2.5rem;
    }

    &::before,
    &::after {
        position: absolute;
        content: "";
    }

    &::after {
        top: calc(50% - 1.3rem);
        left: 1rem;
        width: 2.6rem;
        height: 2.6rem;
        border-radius: 50%;
        background: $green;

        @media screen and (min-width: 1320px) {
            left: 2.4rem;
            width: 3.6rem;
            height: 3.6rem;
        }
    }

    &::before {
        z-index: 1;
        top: calc(50% - .6rem);
        left: 1.9rem;
        transform: rotate(45deg);
        width: .8rem;
        height: .9rem;
        border: solid #fff;
        border-width: 0 .2rem .2rem 0;

        @media screen and (min-width: 1320px) {
            top: calc(50% - .2rem);
            left: 3.7rem;
            transform: rotate(45deg);
            width: 1rem;
            height: 1.1rem;
        }
    }

    &:not(:last-of-type) {
        margin-bottom: 2rem;
    }

    &:last-of-type {
        margin-bottom: 2.5rem;
    }

    &--warning {
        position: relative;
        padding: 1.5rem 2.3rem 1.5rem 4.35rem;
        margin-top: 1.25rem;
        display: block;
        @include OpenSans-Regular();
        font-size: 1.6rem;
        line-height: 2.3rem;
        color: $gray6;
        background: $green;

        @media screen and (min-width: 1320px) {
            padding: 2.7rem 3.3rem 2.7rem 8.7rem;
            font-size: 1.8rem;
            line-height: 2.5rem;
        }

        &::before {
            position: absolute;
            content: "!";
            top: calc(50% - 1.4rem);
            transform: rotate(0) !important;
            left: 1rem;
            width: 2.6rem;
            height: 2.6rem;
            display: flex;
            justify-content: center;
            align-items: center;
            @include OpenSans-Bold();
            font-size: 1.6rem;
            line-height: 2.3rem;
            color: $gray2;
            border-radius: 50%;
            background: #fff;

            @media screen and (min-width: 1320px) {
                top: calc(50% - 1.8rem);
                left: 2.4rem;
                width: 3.6rem;
                height: 3.6rem;
                font-size: 1.8rem;
                line-height: 2.5rem;
            }
        }
    }
}