.list-style {

    li {
        position: relative;

        &::before {
            position: absolute;
            content: "";
            top: 1.2rem;
            left: -2rem;
            transform: translateY(-50%);
            width: 1rem;
            height: 1rem;
            border-radius: 50%;
            background: $green;
    
            @media screen and (min-width: 1320px) {
                top: 50%;
            }
        }
    }
    
}