.alcoholism-price {

    .gray-block {
        margin-bottom: 4rem;

        @media screen and (min-width: 1320px) {
            margin-bottom: 8rem;
        }
    }

    .services-cards {
        margin-bottom: 5.05rem;

        @media screen and (min-width: 1320px) {
            margin-bottom: 10.1rem;
        }
    }

    .service__card {
        padding: 9.1rem 2.5rem 5.6rem 1.8rem;
        background: $gray6;
        border-radius: .5rem;

        .label {
            left: 0;
        }

        .card__text {
            margin-bottom: 1.7rem;
        }
    }

    .card__title--small {
        margin-bottom: 1rem;
    }
}
